/*===============================================
  CKEditor
================================================= */
.admin-skin.cke_chrome {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  border: none;
  box-shadow: none;
  padding: 0;
  margin: 0;
  border-radius: 2px;
  background: #fafafa;
  border-color: #e7e7e7; }

.admin-skin .cke_top {
  background: #fafafa;
  padding: 0px 10px 11px;
  -webkit-border-radius: 3px 3px 0 0;
  border-radius: 3px 3px 0 0;
  border: 1px solid #e7e7e7; }

.admin-skin .cke_toolgroup,
.admin-skin .cke_combo_button {
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 10px;
  margin-bottom: 0;
  box-shadow: none;
  background: #FFF;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  border-color: #DDD; }

.admin-skin a.cke_button {
  padding: 5px 4px; }

.admin-skin .cke_contents {
  border-radius: 0;
  border-color: #e7e7e7; }

.admin-skin .cke_top + .cke_contents,
.admin-skin .cke_contents + .cke_bottom {
  border-top: 0; }

.admin-skin .cke_bottom {
  background: none;
  padding: 6px 4px;
  border: 1px solid #e7e7e7; }

.admin-skin .cke_resizer {
  margin-top: 6px;
  margin-right: 4px;
  border-right-color: #999; }

.cke_editable_inline.cke_show_borders:focus {
  outline-width: 2px;
  outline-style: dashed;
  outline-offset: 6px;
  outline-color: #f6bb42;
  background: #f2f2f2; }

.cke-hide-bottom .cke_bottom {
  display: none; }

.cke_toolbar_break {
  clear: none !important;
  display: block !important; }

/* ==================================================
  Countdown
 ==================================================== */
#counter {
  width: 100%;
  min-height: 200px;
  padding: 30px 20px;
  margin-bottom: 100px; }

.countdown-rtl {
  direction: rtl; }

.countdown-holding span {
  color: #888; }

.countdown-row {
  clear: both;
  width: 100%;
  padding: 0px 2px;
  text-align: center; }

.countdown-show1 .countdown-section {
  width: 98%; }

.countdown-show2 .countdown-section {
  width: 48%; }

.countdown-show3 .countdown-section {
  width: 32.5%; }

.countdown-show4 .countdown-section {
  width: 24.5%; }

.countdown-show5 .countdown-section {
  width: 19.5%; }

.countdown-show6 .countdown-section {
  width: 16.25%; }

.countdown-show7 .countdown-section {
  width: 14%; }

.countdown-section {
  position: relative;
  display: block;
  float: left;
  font-size: 75%;
  text-align: center; }

.countdown-section:after {
  content: "";
  position: absolute;
  width: 5px;
  height: 45%;
  margin-top: 18%;
  top: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.5); }

.countdown-section:last-child:after {
  display: none; }

.countdown-amount {
  color: #FFF;
  font-weight: 200;
  font-size: 90px; }

.countdown-period {
  display: block;
  color: #FFF;
  font-weight: 200;
  font-size: 24px; }

.countdown-descr {
  display: block;
  width: 100%; }

/*===============================================
  DataTables
================================================= */
/* DataTable */
table.dataTable {
  margin: 0 !important; }

/* Table Row Changes */
table.dataTable tr.odd {
  background-color: #fff; }

/* Table Header Changes */
table.dataTable thead th {
  background: #f9f9f9;
  padding: 10px; }

/* Table Footer Changes */
table.dataTable tfoot th {
  border-top: 1px solid #ddd;
  padding: 4px 18px 4px 10px;
  font-weight: 600;
  background: #f2f2f2; }

/* DataTable panel-menu, commonly holds filters */
.dt-panelmenu {
  padding: 10px 14px;
  background: #f8f8f8;
  border-bottom: 1px solid #e5e5e5; }

/* DataTable panel-footer, commonly holds paginator */
.dt-panelfooter {
  padding: 6px 14px;
  background: #f8f8f8;
  border-top: 1px solid #e5e5e5; }

/* DataTable Length */
.dataTables_length {
  float: left; }

.dataTables_length select {
  margin: 0 5px; }

/* DataTable Filter */
.dataTables_filter {
  float: right; }

.dataTables_length label,
.dataTables_filter label {
  margin: 0; }

/* DataTable Search bar */
table.dataTable .form-control {
  width: 100%; }

/* DataTable Footer Elements */
div.dataTables_info {
  float: left;
  padding-top: 12px; }

div.dataTables_paginate {
  margin-top: 5px; }

/* Filter Returned No results Pane */
table.dataTable td.dataTables_empty {
  padding: 50px !important;
  background: #f6f6f6 !important; }

/* Datatables abc filter */
.dt-abc-filter {
  padding: 4px 0; }

.dt-abc-filter span {
  cursor: pointer;
  padding: 0 9px;
  font-size: 12px;
  font-weight: 600;
  color: #4a89dc;
  display: inline-block; }

.dt-abc-filter .abc-label {
  color: #999;
  padding: 0 3px; }

/* Contextual Row Filter Example */
table.dataTable tr.row-label td {
  background: #f6f6f6;
  color: #333;
  font-weight: 600;
  cursor: pointer; }

/*
 * Table Selected/Active Row
 */
.table.dataTable tbody tr.active td,
.table.dataTable tbody tr.active th {
  color: #555;
  border-color: #EEE;
  background-color: #FFFEF0 !important; }

.table.dataTable tbody tr.active:hover td,
.table.dataTable tbody tr.active:hover th {
  color: #555;
  border-color: #EEE;
  background-color: #FAF9DF !important; }

/*
 * DataTable Modals
 *
 */
.DTE .DTE_Header {
  background-color: #fafafa; }

.DTE .DTE_Header h3 {
  margin-top: 5px;
