.DTE .DTE_Body:after {
  clear: both; }

.DTE .DTE_Body:before,
.DTE .DTE_Body:after {
  content: " ";
  display: table; }

.DTE .DTE_Body:after {
  clear: both; }

/*
 * DataTables Editor Addon - Checkbox
 */
.fchild-checkbox.dataTable thead th.sorting_disabled {
